.modelServersBox {
  background-color: #fff;
  border-radius: 0.75rem;
  height: 500px;
  box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.14);
  -webkit-box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.04);
  -moz-box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.04);
  margin-bottom: 2rem;

  .modelServersHead {
    padding-top: 1.6rem;
    padding-left: 1.8rem;
    padding-bottom: 0.75rem;
  }

  .modelServersTable {
    width: 100%;
    display: flex;
  }

  .modelServersTableLabel {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
  }
}

hr {
  margin-left: 2rem;
  margin-right: 2rem;
  color: rgba(0, 0, 0, 0.01);
}

.recentNotebooks {
  background-color: #fff;
  border-radius: 0.75rem;
  box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.14);
  -webkit-box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.04);
  -moz-box-shadow: 0px 0px 3px 2px rgba(0, 0, 0, 0.04);
  height: 400px;

  .recentNotebooksHead {
    padding-left: 1.2rem;
    padding-top: 1.2rem;
  }
}

.table-container {
  border-radius: 0.5rem;
  width: 100%;
  margin: 0 auto;
  margin-top: 35px;
  padding: 0 0 15px 20px;
}

.table-column {
  margin-bottom: 1rem;
}

.tickets-container {
  border-radius: 0.5rem;
}

