.mainDiv {
  display: flex;
  width: 89vw;
  flex-wrap: wrap;
  justify-content: space-between;
}

.info {
  display: flex;
  flex-direction: row;
  width: 63%;
  margin-bottom: 30px;
  margin-left: 40px;
}

.infoText {
  display: flex;
  flex-direction: column;
  justify-content: left;
  width: 37vw;
  justify-content: center;
}

.helpDiv {
  box-shadow: 0 1px 15px rgba(0, 0, 0, 0.04), 0 1px 6px rgba(0, 0, 0, 0.04);
  width: 100%;
  height: 100%;
  padding: 15px;
}

.helpDiv > p {
  color: rgb(156, 154, 154);
}

.btnHelpDiv {
  display: flex;
  flex-direction: column;
  margin-left: 15px;
}

.helpButton {
  background-color: white;
  display: flex;
  justify-content: start;
  align-items: center;
  border: 1px solid rgb(179, 179, 179);
  width: 200px;
  padding: 6px;
  border-radius: 5px;
  margin-bottom: 20px;
  font-size: 13px;
  font-weight: 600;
}

.helpButton:hover {
  background-color: #D3D3D3;
  transform: scale(1.02);
}

.helpButton > i {
  font-size: 18px;
  font-weight: 600;
}

.infoText > h3 {
  font-size: 1.5rem;
  font-weight: 700;
}

.infoText > p {
  font-size: 1.2rem;
}

.infoText > p > span > a {
  color: #5c31ff;
}

.introImg {
  padding: 10px;
  width: 430px;
  max-height: 280px;
  margin-right: 20px;
}

.slackIcon {
  height: 21px;
  margin-right: 6px;
}

.upgradeDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 30vh;
  left: 42vw;
  height: 120px;
  width: 400px;
  box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.4);
  background-color: #fff;
  color: black;
  z-index: 10;
}

.upgradeDiv > p {
  font-size: 20px;
}

.listItem {
  font-size: 17px;
  font-weight: 500;
}

.buildApp {
  margin-top: 20px;
  background-color: #5c31ff;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  padding: 10px;
  width: 300px;
  font-size: 17px;
  font-weight: 500;
  margin-bottom: 20px;
  color: white;
  text-decoration: none;
  border-radius: 5px;
}

.buildApp:hover {
  color: white;
}

.version {
  border: 1px solid #5c31ff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  padding: 10px;
  font-size: 17px;
  font-weight: 500;
  color: #5c31ff;
  border-radius: 5px;
}

.nextStepDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  // align-items: center;
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 30px;
  width: 350px;
}

.community {
  display: flex;
  flex-direction: column;
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 25px;
  width: 350px;
}

.community > p {
  font-size: 15px;
}

.community > a {
  color: #5c31ff;
  font-size: 15px;
}

.liveDiv {
  display: flex;
  flex-direction: column;
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 35px;
  width: 350px;
}
