.navbar {
  color: #fff;
  height: $navbar-height;
  padding: 1.2rem 0;
  #notificationDropdown {
    width: 260px;
    padding: 1.5rem !important;
    height: 280px;
    right: 15px;
  }
  #iconMenuDropdown {
    width: 240px;
    padding: 1.5rem !important;
    height: 280px;
  }
  .icon-menu-item {
    width: 90px;
    display: inline-block;
    text-align: center;
    margin-bottom: 1.7rem;
    color: rgba($primary-color, 0.8);
    i {
      font-size: 28px;
      line-height: 42px;
    }
    span {
      text-align: center;
      padding: 0 10px;
      line-height: 14px;
    }
    &:hover,
    &:focus {
      color: $theme-color-1;
    }
  }
  .menu-button-mobile {
    color: $secondary-color;
    text-align: center;
    margin-left: $main-margin-xs;
    svg {
      height: 12px;
      fill: #ffffff;
    }
    @include respond-below(sm) {
      width: 20px;
    }
  }
  .menu-button {
    color: $theme-color-white;
    width: $main-menu-width;
    text-align: center;
    svg {
      height: 12px;
    }
    .main {
      fill: $primary-color;
      transition: fill $menu-collapse-time;
      width: 10px;
    }
    .sub {
      fill: $primary-color;
      transition: fill $menu-collapse-time;
    }
    &:hover {
      color: $theme-color-1;
    }
    @include respond-below(xl) {
      width: $main-menu-width-lg;
    }
    @include respond-below(lg) {
      width: $main-menu-width-md;
    }
    @include respond-below(sm) {
      width: 60px;
    }
  }
  .navbar-left {
    position: relative;
    flex-basis: 40%;
  }
  .navbar-right {
    flex-basis: 40%;
    text-align: right;
    .rc-switch {
      margin-top: -2px;
    }
    .user {
      margin-right: $main-margin;
      @include respond-below(xl) {
        margin-right: $main-margin-lg;
      }
      @include respond-below(lg) {
        margin-right: $main-margin-md;
      }
      @include respond-below(sm) {
        margin-right: $main-margin-xs;
      }
    }
  }
  .navbar-logo {
    width: 140px;
    height: 55px;
    position: absolute;
    bottom: -4px;
    left: 120px;
    margin: 0 auto;
    .logo {
      width: 100%;
      height: 100%;
      background: url('../../../../assets/img/white.png') no-repeat;
      background-position: left;
      background-size: contain;
    }
    .logo-mobile {
      width: 100%;
      height: 100%;
      background: url('../../../../assets/img/white.png') no-repeat;
      background-position: center center;
      background-size: contain;
    }
    @include respond-below(sm) {
      width: 60px;
    }
    @include respond-below(xxs) {
      width: 20px;
    }
  }
  .language-button {
    background: $background-color;
    border: initial;
    font-size: 0.8rem;
    color: $secondary-color;
    padding: 0.6rem 1rem;
    @include respond-below(xs) {
      padding-left: 0.75rem;
      padding-right: 0.75rem;
    }
  }
  .search {
    position: relative;
    width: $sub-menu-width;
    border-radius: 20px;
    background: $background-color;
    input {
      border: initial;
      background: transparent;
      outline: initial !important;
      padding: 0.5rem 1rem;
      line-height: 1.75;
      font-size: 0.8rem;
      width: 93%;
      color: $primary-color;
    }
    .search-icon {
      font-size: 17px;
      border-radius: 10px;
      color: $separator-color;
      position: absolute;
      width: 40px;
      height: 40px;
      bottom: -10px;
      right: 3px;
      text-align: center;
      cursor: pointer;
      &:hover {
        color: $theme-color-1;
      }
    }
    @include respond-below(lg) {
      width: $sub-menu-width * 0.7;
      input {
        width: 85%;
      }
    }
    @include respond-below(md) {
      width: $sub-menu-width/2;
      input {
        width: 85%;
      }
    }
    @include respond-below(sm) {
      width: 30px;
      height: 30px;
      background: initial;
      margin-left: 0.6rem;
      color: rgba($primary-color, 0.7);
      input {
        display: none;
      }
      .search-icon {
        font-size: 17px;
        width: 30px;
        height: 30px;
        bottom: -3px;
        right: 0;
        color: inherit;
      }
      &.mobile-view {
        display: block;
        width: 100%;
        position: fixed;
        z-index: 2;
        background: $foreground-color;
        left: 0;
        top: 0;
        height: $navbar-height-xs;
        margin-left: 15px;
        input {
          display: block;
          width: 100%;
          height: 70px;
          padding-left: 0;
        }
        span {
          top: 50%;
          transform: translateY(-50%);
          right: 25px;
        }
      }
    }
  }
  .header-icons {
    margin-right: 1rem;
    @include respond-below(xs) {
      margin-right: 0;
    }
  }
  .header-icon {
    font-size: 16px;
    color: $theme-color-white;
    padding-left: 0.6rem;
    padding-right: 0.6rem;
    vertical-align: initial;
    @include respond-below(xs) {
      padding-left: 0.3rem;
      padding-right: 0.3rem;
    }
    &:hover {
      color: $theme-color-1;
    }
    &#fullScreenButton i:last-of-type {
      display: none;
    }
    &.notificationButton {
      .count {
        font-size: 9px;
        color: $theme-color-1;
        border: 1px solid $theme-color-1;
        border-radius: 10px;
        position: absolute;
        width: 18px;
        height: 15px;
        text-align: center;
        font-weight: 700;
        top: -4px;
        right: 0px;
        line-height: 14px;
        @include respond-below(xs) {
          right: -1px;
        }
      }
    }
  }
  .user {
    color: lighten($primary-color, 30%);
    position: relative;
    button {
      color: $primary-color;
    }
    .name {
      color: $button-text-color;
    }
    img {
      margin-left: 10px;
      border-radius: 30px;
      width: 40px;
    }
    @include respond-below(md) {
      .name {
        display: none;
      }
    }
    @include respond-below(sm) {
      img {
        width: 30px;
      }
      margin-left: initial;
      &:after {
        font-size: 11px;
        width: 14px;
        height: 14px;
        bottom: -3px;
        right: -3px;
      }
    }
  }
  @include respond-below(xl) {
    height: $navbar-height-lg;
  }
  @include respond-below(lg) {
    height: $navbar-height-md;
    padding: 1rem 0 1rem 0;
  }
  @include respond-below(sm) {
    height: $navbar-height-xs;
    padding: 10px 0;
  }
}
.rtl {
  .navbar {
    .navbar-right {
      text-align: left;
    }
    .menu-button-mobile {
      margin-right: 15px;
      margin-left: initial;
    }
    .menu-button {
      text-align: center;
    }
    .icon-menu-item {
      text-align: center;
      i {
        text-align: center;
      }
      span {
        text-align: center;
      }
    }
    .search {
      .search-icon {
        right: initial;
        left: 5px;
      }
      @include respond-below(sm) {
        margin-right: 0.6rem;
        margin-left: initial;
        input {
          display: none;
        }
        &.mobile-view {
          margin-left: initial;
          margin-right: 15px;
          input {
            display: block;
            width: 100%;
            height: 70px;
            padding-left: 0;
          }
          span {
            left: 15px;
            right: initial;
          }
        }
      }
    }
    .navbar-right {
      text-align: left;
      .user {
        margin-left: $main-margin;
        margin-right: 0;
        @include respond-below(xl) {
          margin-left: $main-margin-lg;
          margin-right: 0;
        }
        @include respond-below(lg) {
          margin-left: $main-margin-md;
          margin-right: 0;
        }
        @include respond-below(sm) {
          margin-left: $main-margin-xs;
          margin-right: 0;
        }
        img {
          margin-right: 10px;
          margin-left: initial;
        }
      }
      .header-icons {
        margin-right: initial;
        margin-left: 0.5rem;
        @include respond-below(xs) {
          margin-left: 0;
        }
      }
      #notificationButton {
        .count {
          text-align: center;
          @include respond-below(xs) {
            left: -1px;
          }
        }
      }
      #userDropdown,
      #userDropdown * {
        text-align: left;
      }
    }
  }
}

.header {
  position: relative;
  right: 0;
  left: 0;
  width: 100%;
  z-index: 1001;
}

.header-section {
  position: relative;
  z-index: 1;
  background-color: #fff;
}

.header-section-divider {
  border-bottom: 0.0625rem solid rgba(255, 255, 255, 0.2);
}

.header-box-shadow .header-section {
  box-shadow: 0 1px 10px rgba(140, 152, 164, 0.125);
}

.header-box-shadow-on-scroll.scrolled .header-section {
  box-shadow: 0 1px 10px rgba(140, 152, 164, 0.125);
}

.navbar-brand,
.navbar-brand > img {
  width: 7.5rem;
}

/* Navbar Toggler */
.navbar-toggler {
  background-color: #f8fafd;
}

.navbar-toggler .navbar-toggler-default {
  display: flex;
}

.navbar-toggler .navbar-toggler-toggled {
  display: none;
}

.navbar-toggler[aria-expanded="true"] .navbar-toggler-default {
  display: none;
}

.navbar-toggler[aria-expanded="true"] .navbar-toggler-toggled {
  display: flex;
}

/* Nav Item */
.header .navbar-nav-item:first-child .nav-link {
  padding-left: 0;
}

.navbar-nav-item:hover .nav-link, .navbar-nav-item:focus .nav-link {
  color: #4b2be3;
}

/* Nav Link */
.header .navbar-nav-item.active .nav-link,
.header .nav-link.active {
  color: #4b2be3;
}

.header .nav-link {
  color: #677788;
}

.header .nav-link-toggle {
  display: flex;
  align-items: center;
}

.header .nav-link-toggle::after {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  background: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='%23677788' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12.72,15.78a.75.75,0,0,1-.53.22h-.38a.77.77,0,0,1-.53-.22L6.15,10.64a.5.5,0,0,1,0-.71l.71-.71a.49.49,0,0,1,.7,0L12,13.67l4.44-4.45a.5.5,0,0,1,.71,0l.7.71a.5.5,0,0,1,0,.71Z'/%3E%3C/svg%3E") no-repeat right center/1rem 1rem;
  content: "";
  margin-left: 0.5rem;
  transition: 0.3s ease;
}

/* Dropdown */
.header .navbar-nav .dropdown-menu {
  margin-top: -0.125rem;
}

.header .navbar-nav .dropdown-menu.hs-mega-menu {
  padding-top: 0;
  padding-bottom: 0;
}

.header .navbar-nav .dropdown-item {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.header .dropdown-item-toggle {
  display: flex;
  align-items: center;
}

.header .dropdown-item-toggle::after {
  display: inline-block;
  width: 1rem;
  height: 1rem;
  background: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='%23677788' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.78,11.28a.75.75,0,0,1,.22.53v.38a.77.77,0,0,1-.22.53l-5.14,5.13a.5.5,0,0,1-.71,0l-.71-.71a.49.49,0,0,1,0-.7L13.67,12,9.22,7.56a.5.5,0,0,1,0-.71l.71-.7a.5.5,0,0,1,.71,0Z'/%3E%3C/svg%3E") no-repeat right center/1rem 1rem;
  content: "";
  margin-left: auto;
  transition: 0.3s ease;
}

.header .hs-mega-menu .dropdown-item {
  padding-right: 0;
  padding-left: 0;
}

[class*="navbar-expand"] .dropdown-item {
  background-color: transparent;
}

[class*="navbar-expand"] .dropdown-item.active:not(:focus):not(:active), [class*="navbar-expand"] .dropdown-item:hover {
  color: #4b2be3;
  background-color: transparent;
}

/* Promo */
.navbar-promo {
  background-color: #f8fafd;
}

.navbar-promo-inner {
  color: inherit;
  padding: 2rem 2rem;
}

.navbar-promo-item {
  margin-left: 1.5rem;
  margin-right: 1.5rem;
}

.navbar-promo-item ~ .navbar-promo-item {
  border-top: 0.0625rem solid #e7eaf3;
}

.navbar-promo-link {
  display: block;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
}

.navbar-promo-link.disabled {
  opacity: .7;
  pointer-events: none;
}

.navbar-promo-link:hover .navbar-promo-title {
  color: #4b2be3;
}

.navbar-promo-card {
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;
  background-clip: border-box;
}

.navbar-promo-card-deck {
  display: block;
}

.navbar-promo-card-deck:not(:last-child) .navbar-promo-item {
  border-bottom: 0.0625rem solid #e7eaf3;
}

.navbar-promo-card-deck .navbar-promo-item {
  border-top: none;
}

.navbar-promo-card-body {
  flex: 1 1 auto;
}

.navbar-promo-icon {
  width: 100%;
  max-width: 2.75rem;
  margin-right: 1rem;
}

.navbar-promo-title {
  display: block;
  color: #1e2022;
  font-size: 1rem;
  font-weight: 600;
}

.navbar-promo-text {
  display: block;
  font-size: 0.875rem;
  color: #677788;
}

.navbar-promo-footer {
  background-color: #f8fafd;
  border-bottom-right-radius: 0.3125rem;
  border-bottom-left-radius: 0.3125rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.navbar-promo-footer-item {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.navbar-promo-footer-text {
  display: block;
  font-size: 0.8125rem;
}

.navbar-promo-footer-ver-divider {
  position: relative;
}

.navbar-promo-footer-ver-divider::before {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  border-right: 0.0625rem solid #e7eaf3;
  content: "";
}

/* Banner */
.navbar-banner {
  position: relative;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  border-bottom-left-radius: 0.3125rem;
}

.navbar-banner::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(30, 32, 34, 0.5);
  border-bottom-left-radius: 0.3125rem;
  content: "";
}

.navbar-banner-content {
  z-index: 1;
  width: 100%;
  text-align: center;
  padding: 2rem 2rem;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}

/* Product Banner */
.navbar-product-banner {
  background-color: #f8fafd;
  border-bottom-right-radius: 0.3125rem;
  border-bottom-left-radius: 0.3125rem;
  padding-right: 1.5rem;
}

.navbar-product-banner-content {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

/*------------------------------------
  Header Helpers
------------------------------------*/
.header-invisible {
  display: none;
}

.header-moved-up {
  transform: translate3d(0, -100%, 0);
}

.header-moved-up.header-hide-topbar {
  transform: translate3d(0, -36px, 0);
}

.header-faded {
  opacity: 0;
  visibility: hidden;
}

.header-section-hidden {
  position: relative;
}

.header[data-hs-header-options*="fixMoment"] {
  transition: 0.3s ease;
}

.header.header-untransitioned {
  transition: none;
}

.header.js-header-fix-moment {
  position: fixed;
  top: 0;
  bottom: auto;
}

.header.js-header-fix-moment .header-hide-content {
  display: none;
}

.header-fix-top {
  position: fixed;
}

.header.header-fix-top[data-hs-header-options*="effectCompensation"] {
  transition: none;
}

/*------------------------------------
  Nav Alignments
------------------------------------*/
@media (min-width: 992px) {
  .header .navbar-body,
  .header .navbar-nav {
    margin-left: auto;
  }
  .left-aligned-navbar .navbar-body,
  .left-aligned-navbar .navbar-nav {
    width: 100%;
    margin-left: 0;
    margin-right: auto;
  }
  .left-aligned-navbar .navbar-brand {
    margin-left: 0;
    margin-right: 3rem;
  }
  .left-aligned-navbar .navbar-nav-last-item {
    margin-left: auto;
  }
  .center-aligned-navbar .navbar-body,
  .center-aligned-navbar .navbar-nav {
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 768px) {
  .center-aligned-navbar.header-abs-top-sm .navbar-body,
  .center-aligned-navbar.header-abs-top-sm .navbar-nav {
    margin-left: auto;
    margin-right: auto;
  }
}

/*------------------------------------
  Header Default
------------------------------------*/
.navbar-nav-scroll {
  max-width: 100%;
  height: 4.625rem;
  overflow: hidden;
}

.navbar-nav-scroll .navbar-nav {
  overflow-x: auto;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}

