  justify-content: flex-end;
}

.navbar-expand .navbar-nav-wrap-brand {
  margin-right: auto;
}

.navbar-expand .navbar-nav-wrap-content {
  text-align: center;
  margin-right: 0.5rem;
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .navbar-expand-md .navbar-nav-wrap-brand {
    flex: 0 0 100%;
